<template>

    <li :class="getPosition">
      <div class="img-profile-small">
          <a data-toggle="modal" data-target=".profileModal"><img src="../assets/images/profile_img_2.png"></a>
      </div>
      <div class="triangle"></div>
      <div class="message">{{ message.text }}</div>
      <div class="sent-time">{{ message.createdAt | moment("lll") }}</div>
    </li>

</template>
<script>
/* eslint-disable */
  export default {
    name: 'Message',
    // created(){
    //   console.log(message)
    // },
    props: {
      message: {
        //type: Object
      }
    },
    computed: {
      getPosition () {
        // console.log(this.message)
        if (this.message.senderType != "user") {
          return 'me'
        } else {
          return 'you'
        }
      }
    }
  }
</script>
<style scoped>
.my.message {
  text-align: right;
}
.message-container {
  border: 1px solid silver;
}
</style>


